import React from 'react'
import { Container} from '@material-ui/core'
import Carousel from 'react-bootstrap/Carousel'


export const Home = ({pallete, fonts}) => { 
    return (
        <>
            <h1 style={{fontFamily: fonts.title, color: pallete.contrastText}}>
                Featured
            </h1>
            <Carousel >
            <Carousel.Item >
                <img
                className="d-block w-100"
                style={{height:300}}
                src={require("./images/carousel.png")}
                alt="First slide"
                />
                <Carousel.Caption>
                <h3>Welcome!</h3>
                <p>This is Ian's new website. Feel free to browse around and change the colors and fonts.</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item >
                <img
                className="d-block w-100"
                style={{height:300}}
                src={require("./images/carousel.png")}
                alt="Third slide"
                />

                <Carousel.Caption>
                <h3>Ian's Bio</h3>
                <p>Find out about who he is.</p>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item >
                <img
                className="d-block w-100"
                style={{height:300}}
                src={require("./images/carousel.png")}
                alt="Third slide"
                />

                <Carousel.Caption>
                <h3>Revolve</h3>
                <p>A high action spinning top fighter.</p>
                </Carousel.Caption>
            </Carousel.Item>
            </Carousel>
        </>
    )
};

export default Home